import { Component, Input, OnInit } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IchangeValues } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsDropdownLovComponent } from '../ps-dropdown-lov.component';
import { IOptionsPsDropdownLov } from '../ps-dropdown-lov.component.interfaces';
import { IOptionsPsLovBeneficiaryAccountTypeExposed } from './ps-lov-beneficiary-account-type.component.interface';

@Component({
  selector: 'ps-lov-beneficiary-account-type',
  templateUrl: './ps-lov-beneficiary-account-type.component.html',
  styleUrls: ['./ps-lov-beneficiary-account-type.component.scss'],
})
export class PsLovBeneficiaryAccountTypeComponent extends PsDropdownLovComponent implements OnInit {

  @Input() options: IOptionsPsLovBeneficiaryAccountTypeExposed;
  defaultSelectOptions: IOptionsPsDropdownLov = {
    iconLocation: PsCommonBusinessSettings.ICON_LOCATION_GENERAL,
    iconExtension: PsCommonBusinessSettings.IMG_GENDER_EXTENSION,
    lovTypeId: CommonBussinessConstant.LOV_TYPE_ID_BENEFICIARY_ACCOUNT_TYPE
  };

  constructor(commonProvService: PsCommonService, logger: LoggerService) {
    super(commonProvService, logger);
  }

  ngOnInit() {

    this.commonProv.copyObject(this.defaultSelectOptions, this.options, false);
    this.defaultSelectOptions.labelKey = this.commonProv.translate('beneficiary_account_type_key');
    this.defaultSelectOptions.placeHolder = this.commonProv.translate('select_beneficiary_account_type_key');
  }
  onChangeLov(values: IchangeValues) {
    this.onPsChange.emit(values);
  }
}
