import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsComplexInstantPayAddressExposed } from './ps-complex-instant-pay-address.component.interface';

@Component({
  selector: 'ps-complex-instant-pay-address',
  templateUrl: './ps-complex-instant-pay-address.component.html',
  styleUrls: ['./ps-complex-instant-pay-address.component.scss'],
})
export class PsComplexInstantPayAddressComponent extends PsBaseFieldComponent implements OnInit {
  @Input() options: IOptionsPsComplexInstantPayAddressExposed;
  @Output() onHandlerChange: EventEmitter<any> = new EventEmitter<any>();
  @Output() onAddressChange: EventEmitter<any> = new EventEmitter<any>();

  constructor(public commonProv: PsCommonService, public loggerP: LoggerService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    super.init();
  }

  handlerChanged(event){
this.onHandlerChange.emit(event);
  }

  addressChanged(event){
    this.onAddressChange.emit(event);
  }

}
