import { Component, Input, OnInit } from '@angular/core';
import { IPspHandlerRequest } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsSelectDropdownComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-select/ps-select-dropdown/ps-select-dropdown.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IchangeValues, IOptionsPsSelectDropdown, IPsSelect } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsDropdownPspHandlerExposed } from './ps-dropdown-psp-handler.component.interface';

@Component({
  selector: 'ps-dropdown-psp-handler',
  templateUrl: './ps-dropdown-psp-handler.component.html',
  styleUrls: ['./ps-dropdown-psp-handler.component.scss'],
})
export class PsDropdownPspHandlerComponent extends PsSelectDropdownComponent implements OnInit {

  @Input() options: IOptionsPsDropdownPspHandlerExposed;

  defaultSelectOptions: IOptionsPsSelectDropdown = {};

  constructor(commonProv: PsCommonService, loggerP: LoggerService, private omniPull: OmniPullService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultSelectOptions, this.options, false);
    this.loadPspHandlers();
  }

  //to be updated by right properties when service is done
  async loadPspHandlers() {
    const handlersRequest: IPspHandlerRequest = {};

    const result = await this.omniPull.returnPspHandlers(handlersRequest).catch(error => {
      this.logger.error('Error: While fetching handlers in PsDropdownPspHandlerComponent :', error);
    });
    this.defaultSelectOptions.listOfOptions = []
    if (result && result.gridModel) {
      for (const iterator of result.gridModel) {
        const eachHandler: IPsSelect = {
          itemValue: iterator.IPN_PSPS_ID.toString(),
          description: '@' + iterator.PSP_HANDLES,
          selectedObj: iterator
        };
        this.defaultSelectOptions.listOfOptions.push(eachHandler);
      }
    }
  }


  onHandlerChange(values: IchangeValues) {
    this.onPsChange.emit(values);
  }
}

